import { Helmet } from 'react-helmet-async';

import { useParams } from 'src/routes/hooks';

import ACLGuard from 'src/auth/guard/acl-guard';
import { useAuthContext } from 'src/auth/hooks';
import { useGetAppointmentConfig } from 'src/api';

import { LoadingScreen } from 'src/components/loading-screen';

import AppointconfigDetailView from 'src/sections/employee/appoint-config/view/appoint-config-detail';

// ----------------------------------------------------------------------

export default function EmployeeAppointconfigPage() {
  const params = useParams();
  const { coid } = params;
  const { data, loading, refetch } = useGetAppointmentConfig(coid);
  const { user } = useAuthContext();

  return (
    <ACLGuard category="work_group" subcategory="appointment_configs" acl="update">
      <Helmet>
        <title> {user?.employee?.name_english || 'employee'} : Appointment Config Detail </title>
        <meta name="description" content="meta" />
      </Helmet>
      {loading && <LoadingScreen />}
      {!loading && (
        <AppointconfigDetailView
          appointmentConfigData={data || null}
          refetch={refetch}
          loading={loading}
        />
      )}
    </ACLGuard>
  );
}
